@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,800&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

a{
  text-decoration: none;
}


h1, h2, p {
  margin:0;
  padding:0;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

body{
  font-family: 'Montserrat', sans-serif;
  height:100%;
}


.container{
  max-width: 1440px;
  width:100%;
  margin:0 auto;
}

nav {
  height:10vh;
  background-color: #e4e4e4;
  color:black;
  display:flex;
  align-items: center;
  padding: 1rem;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1440px;
  width:100%;
  margin:0 auto;
}

.wrapper h1 {
  font-size:1rem;
  font-weight:600;
}

.country {
  margin:0 auto;
  width: 15rem;
  height:20rem;
  background-color: #e4e4e4;
  margin-bottom: 2rem;
  border-radius:5px;
}

.country img{
  height:160px;
}

.info_container{
  padding-left: 1rem;
  color:black;
}

.info_container h2 {
  font-size: 1rem;
  margin-bottom: 1rem;
  margin-top:1rem;
}

.info_container p {
    font-size: 0.875rem;
    line-height: 2;
    font-weight: 600;
}

.info_container p span {
  font-weight: 400;
}

.search_countries input, .select_countries select, .country, .back, nav{
  box-shadow: 0px 3px 12px 1px rgba(0,0,0,0.5);
}

.search_countries {
  margin-bottom: 3rem;
  margin-top: 3rem;
  width:100%;
  height: 50px;
  position: relative;
  display: block;
}

.search_countries input {
  width: 100%;
  margin-bottom: 3rem;
  height: 50px;
  padding-left: 3rem;
}

.search_countries::after {
  content: "search";
  font-family: 'material icons';
  font-size: 1.3rem;
  position: absolute;
  left:1rem;
  top:50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  pointer-events: none;
}

.select_countries{
  margin-bottom: 3rem;
  margin-top: 3rem;
  width:50%;
  height:50px;
  position: relative;
  display: block;
}

.select_countries select{
  width: 100%;
  height: 50px;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
}


.select_countries::after {
  content: "keyboard_arrow_down";
  font-family: 'material icons';
  position: absolute;
  right:1rem;
  top:50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  pointer-events: none;
  color: black;
}

input, select {
  width: 100%;
  height: 36px;
  padding-left:1rem;
  outline: none;
  border-radius: 5px;
  border:none;
  font-family: 'Montserrat', sans-serif;
}

.theme .material-icons {
  font-size:1.3rem;
}

.theme {
  background-color: transparent;
  border:none;
  outline:none;
  cursor:pointer;
  color:white;
}

.theme span {
  display:flex;
  align-items: center;
}

@media (min-width: 619px) {

  .wrapper h1 {
      font-size: 1.6rem;
      font-weight: 800;
  }

  #mode {
      font-size: 1rem;
  }

  .atlas {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 1rem;
      grid-row-gap: 4rem;
  }

  .search_bar {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
  }

  .search_countries {
      margin-bottom: 3rem;
      width: 40%;
  }

  .select_countries {
      width: 30%;
  }

}

@media (min-width: 799px) {

  .atlas {
      grid-template-columns: repeat(3, 1fr);
  }

  .select_countries {
      width: 25%;
  }

}

  @media (min-width: 1199px) {

  .atlas {
          grid-template-columns: repeat(4, 1fr);
  }

  .world-container, header {
      padding-left:4rem;
      padding-right:4rem;
  }

  .select_countries {
      width: 15%;
  }

  .search_countries {
      width: 25%;
  }

}
.state-container {
    margin-top: 2rem;
    padding-bottom: 4rem;
    min-height: 100%;
  }
  
  .part-one p, 
  .part-two p, 
  .part-three p {
    font-weight: 600;
    padding-top:0.4rem;
    padding-bottom:0.4rem;
  }
  
  .part-one p span, 
  .part-two p span, 
  .part-three p span{
    font-weight: 400;
  }
  
  .info-state {
    padding:1rem;
  }
  
  .flag-state{
    padding:1rem;
  }

  .flag-state img{
    max-width:100%;
    width:100%;
    display: block;
    border: 1px solid black;
}
  
  .part-one{
    margin-top:1rem;
  }
  
  .part-two{
    margin-top:1rem;
    margin-bottom:1rem;
  }
  
  .info-state h2 {
    font-size: 1.6rem;
  }

  .back {
    height:40px;
    width:120px;
    border-radius: 5px;
    border:none;
    text-align: center;
    outline: none;
    margin-top:3rem;
    margin-left:1rem;
    cursor: pointer;
  }
  
  .material-icons {
    font-size: 1rem;
    margin-right:0.5rem;
  }

 .back span {
    display:flex;
    justify-content: center;
    align-items: center;
  }


@media (min-width: 959px) {

    .state-container {
        display: flex;
    }

    .flag-state {
        width:55%;
    }

    .info-state{
        width:45%;
        align-self: center;
        margin-left:1rem;
    }

    .part-three {
        margin-top: 1rem;
    }

}

  @media (min-width: 1143px) {

    .state-container, .main-section {
        display: flex;
    }

    .flag-state {
        width:50%;
    }

    .info-state{
        width:50%;
        align-self: center;
        margin-left:1rem;
    }

    .part-three {
        margin-top: 2rem;
    }

    .part-two {
        margin-left:3rem;
    }


}

@media (min-width: 1439px) {

    .state-container, .main-section {
        display: flex;
    }

    .flag-state {
        width:40%;
    }

    .info-state{
        width:60%;
        align-self: center;
        margin-left:4rem;
    }

    .part-two {
        margin-left:5rem;
    }

}
